.mdlext-collapsible {
  box-sizing: border-box;
  cursor: pointer;
}

.mdlext-collapsible-group,
.mdlext-collapsible-region {
  box-sizing: border-box;

  &[hidden] {
    @include mdlext-visually-hidden;
    pointer-events: none;
  }
}



